import React from "react";
// import delay from "delay";
import deepPurple from "@material-ui/core/colors/deepPurple";
import { Keyframes } from "react-spring/renderprops";

const Container = Keyframes.Spring({
  // Single props
  show: { opacity: 1 },
  // Chained animations (arrays)
  showAndHide: [{ opacity: 1 }, { opacity: 0 }, { opacity: 1 }],
  // Functions with side-effects with access to component props
  wiggle: async (next, cancel, ownProps) => {
    await next({
      from: { x: 200, color: deepPurple[900] },
      to: { x: 0, color: "black" },
      config: { tension: 170, friction: 70 }
    });
    await next({
      from: { x: 0, color: "black" },
      to: { x: 200, color: deepPurple[900] },
      config: { tension: 170, friction: 30 }
    });
    await next({
      from: { y: 1 },
      to: { y: 0 },
      config: { tension: 170, friction: 30 }
    });
    await next({
      visibility: "hidden"
    });
  }
});

